'use client';

import Sidebar from "@/components/sidebar";
import MobileTabBar from "@/components/mobile-tab-bar";
import MutualsDrawer from "@/components/mutuals-drawer";
import { usePathname } from "next/navigation";

interface MainLayoutProps {
  children: React.ReactNode;
}

export default function MainLayout({ children }: MainLayoutProps) {
  const pathname = usePathname();
  const hideDrawer = [
    "/current-party",
    "/live-party",
    "/hostparty",
    "/settings",
  ].some((p) => pathname?.startsWith(p));

  return (
    <div className="flex min-h-screen bg-background" >
      {/* Sidebar (hidden on mobile) */}
      <div className="hidden sm:block z-[70]">
        <Sidebar />
      </div>
      {/* Main Content; add left padding equal to collapsed width to avoid content underlap */}
      <main className="relative flex-1 md:pl-16 dashboard-gradient">
        <div className="h-full">
          {children}
        </div>
      </main>
      {/* Right mutuals drawer (hidden on specific routes) */}
      {!hideDrawer && (
        <MutualsDrawer />
      )}
      {/* Mobile bottom navigation */}
      <div className="sm:hidden">
        <MobileTabBar />
      </div>
    </div>
  );
}
